/* eslint-disable */
import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Button, platform, IOS, Group, Cell, List, FormStatus, Div} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Pet = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="persik">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
					
		</PanelHeader>
		<Div>
		<FormStatus title=" " state="error">
            ,   . 
        </FormStatus> 
		</Div>
        <Group title=" ">
            <List>
                <Cell description="1000 " asideContent={<Button onClick={() => props.buy(1)}></Button>} ></Cell>
                <Cell description="25000 " asideContent={<Button onClick={() => props.buy(2)}></Button>} ></Cell>
                <Cell description="95000 " asideContent={<Button onClick={() => props.buy(3)}></Button>} ></Cell>
                <Cell description="150000 " asideContent={<Button onClick={() => props.buy(4)}></Button>} ></Cell>
                <Cell description="250000 " asideContent={<Button onClick={() => props.buy(5)}></Button>} ></Cell>
            </List>
        </Group>
	</Panel>
);

Pet.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Pet;
